@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@400;500;600&family=Montserrat:wght@400;500;600;700&display=swap);
*{color:var(--text)}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:var(--background) !important}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}



:root{--text: white;--background: #1f2022;--header: #84B7DB;--footer: var(--header);--about-me: #F1F1F1;--grid-background: var(--background);--grid-header-background: var(--background);--grid-header-text: #F1F1F1;--grid-element-background: #354560;--grid-element-border: #6f6f6f;--grid-element-header: rgb(169 162 228 / 100%);--chainlink-icon-background: rgba(43, 43, 43, 0.3);--link-color: #1890ff;--grid-element-subheader: darkgrey}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}


