6%}#project-grid .Medium{width:33%}#project-grid .FourByOne{width:49.8%}}.GridElement{padding:5px}.GridElement .GridElementInternal{padding:9px;border-radius:5px;border:var(--grid-element-border);border-width:2px;border-style:solid;background:var(--grid-element-background);box-shadow:0 1px 2px rgba(0,0,0,0.15);transition:box-shadow 0.3s ease-in-out}.GridElement .GridElementInternal:hover{box-shadow:0 2px 5px rgba(0,0,0,0.3)}.GridElement h2{font-family:"Fira Code", monospace;font-weight:400;margin-bottom:0px;color:var(--grid-element-header);line-height:normal}.GridElement h3{line-height:normal;color:var(--grid-element-subheader);margin-bottom:3px}.GridElement p{font-family:"Montserrat", sans-serif;font-size:16px;color:var(--text);margin-bottom:0}.OneWide{display:flex;flex-direction:column;width:available}.OneWide img{width:100%;margin-bottom:5px}.Medium img{width:100%;margin-bottom:5px}@media (max-width: 999px){.Medium{display:flex;flex-direction:column;width:available}.Medium img{width:100%;margin-bottom:5px}}@media (min-width: 1000px){.Medium .image{float:left;width:50%;-webkit-shape-outside:inset(0);shape-outside:inset(0)}.Medium .image img{margin-bottom:0}.Medium .description{display:inline}.Medium .description p{text-align:justify}.Medium .description h2 h3{display:inline}}@media (min-width: 1561px){.Medium .image{width:40%;padding-right:10px}}.FourByOne .GridElementInternal{display:flex;align-items:center}.FourByOne .GridElementInternal .image{flex:1 1}.FourByOne .GridElementInternal .image img{width:100%;max-width:100%}.FourByOne .GridElementInternal .description{min-width:20ch;width:30ch}@media (min-width: 1001px){.FourByOne .GridElementInternal{flex-direction:row}}@media (max-width: 1000px){.FourByOne .GridElementInternal{flex-direction:column}.FourByOne .GridElementInternal .description{width:95%}}.LinkIcon{position:absolute;right:12px;top:12px;font-size:24px;color:black;background:var(--chainlink-icon-background);border-radius:5px}

#about-me-parent{margin-left:10px;margin-right:10px;margin-bottom:10px;display:flex;flex-direction:row;justify-content:center}#about-me-parent b{font-weight:500}#about-me-parent .about-me-card{border-radius:5px;margin:0 5px 0;padding:10px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;color:var(--text)}#about-me-parent #about-me{border-radius:5px;padding:15px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}#about-me-parent #about-me #info-parent{display:grid;grid-template-columns:1fr 1fr}@media screen and (max-width: 660px){#about-me-parent #about-me #info-parent{grid-template-columns:1fr}}#about-me-parent #about-me h1{font-family:"Fira Code", monospace;font-weight:500;font-size:46px;color:var(--text)}#about-me-parent #about-me h2{font-family:"Fira Code", monospace;line-height:normal;margin-bottom:0;font-weight:400;color:var(--text);text-align:left}#about-me-parent #about-me p{font-family:"Montserrat", sans-serif;font-size:16px;margin:0}#about-me-parent #about-me ul{text-align:left;padding-left:40px;margin-bottom:0}#about-me-parent #about-me li{font-family:"Montserrat", sans-serif;font-size:18px;list-style-type:symbols("+");list-style-position:inside}button{background:none !important;border:none;padding:0 !important;color:var(--link-color);cursor:pointer}a{color:var(--link-color)}

